.artist-profile {
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  -ms-flex-direction: column;
      flex-direction: column;
  top: 8vh;
  height: 92vh;
  width: 100%;
}

.artist-info {
  background-image: url('../assets/profile-background.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  display: -ms-flexbox;
  display: flex;
  font-family: 'Samsung Sharp Sans Regular Regular';
  height: 50%;
  width: 100%;
}

.artist-user,
.artist-bio,
.artist-stats {
  -ms-flex-align: center;
      align-items: center;
  background-color: rgba(0, 0, 0, 0.2);
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  width: calc(100%/3);
}

.artist-user {
  font-family: 'SamsungSharpSans-Bold';
  -ms-flex-direction: column;
      flex-direction: column;
}

.artist-tag {
  height: 20vw;
  -o-object-fit: fill;
     object-fit: fill;
  width: 32vw;
}

.victory-chart {
  width: 100%;
}

.artist-profile-images {
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  grid-template-rows: auto;
  height: auto;
  overflow-y: scroll;
  width: 100%;
}

.profile-imgs {
  height: 300px;
  width: 300px;
}



